import { turbo, TurboTool } from "../../../../../build/turbodombuilder.esm";
export class SelectTool extends TurboTool {
    constructor() {
        super(...arguments);
        this.toolName = "select";
    }
    onActivation() {
        turbo(this).toggleClass("selected", true);
    }
    onDeactivation() {
        turbo(this).toggleClass("selected", false);
    }
    drag(e, target) {
        if ("origin" in target && typeof target.origin === "object") {
            target.origin = e.scaledDeltaPosition.add(target.origin).object;
            return true;
        }
    }
}
