___CSS_LOADER_EXPORT___.push([module.id, `:root {
    --bg: #01163b;
    --bg-gradient: linear-gradient(51deg, #01163b 0%, #440a61 50%, #4d0527 100%);
    --surface: rgba(255, 255, 255, 0.07);
    --surface-2: rgba(255, 255, 255, 0.03);
    --border: rgba(255, 255, 255, 0.14);

    --font-family: "Roboto";
    --text: rgba(220, 232, 255, 0.9);
    --text-2: rgba(234, 242, 255, 0.8);
    --text-muted: rgba(212, 224, 244, 0.6);

    --accent: #ff6bd6;
    --accent-2: #5ee7ff;
    --accent-contrast: #0b0f1e;

    --shadow-1: 0 10px 30px rgba(0, 0, 0, 0.45);
    --focus-ring: 0 0 0 2px rgba(94, 231, 255, 0.55);
    --blur: 10px;

    --gap-big: 28px;
    --gap-medium: 18px;
    --gap-small: 12px;
    --gap-tiny: 4px;

    --radius-big: 16px;
    --radius-small: 12px;
    --radius-tiny: 8px;
}


body {
    width: calc(100vw - var(--gap-big) * 2);
    height: calc(100vh - var(--gap-big) * 2);
    overflow: hidden;

    margin: 0;
    padding: var(--gap-big);

    background: var(--bg);
    background: var(--bg-gradient);
}

* {
    margin: 0;
    padding: 0;
    color: var(--text);
    font-family: var(--font-family), sans-serif;
}

.turbo-icon {
    display: block;
    width: 1.5rem;
    height: 1.5rem;
}

.turbo-icon > svg {
    fill: var(--text);
    width: 100%;
    height: 100%;
}

#canvas {
    position: absolute;
    display: block;
    overflow: visible;
    top: 0;
    left: 0;
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
}

#canvas * {
    -webkit-backface-visibility: hidden;
    -moz-backface-visibility: hidden;
    backface-visibility: hidden;
