import { ClickMode, randomColor, turbo, TurboTool } from "../../../../../build/turbodombuilder.esm";
import { NewPlaylistModel } from "./newPlaylist.model";
import { playlist } from "../../playlist/playlist";
export class NewPlaylistTool extends TurboTool {
    constructor() {
        super(...arguments);
        this.toolName = "newPlaylist";
        this.clickMode = ClickMode.left;
    }
    onActivation() {
        turbo(this).toggleClass("selected", true);
    }
    onDeactivation() {
        turbo(this).toggleClass("selected", false);
    }
    click(e, target) {
        if (target !== this.model.target && target !== document.body)
            return;
        turbo(this.model.target).addChild(playlist({
            data: {
                color: randomColor(),
                name: "Playlist " + NewPlaylistModel.playlistCount,
                songs: [],
                origin: e.scaledPosition
            }
        }));
        NewPlaylistModel.playlistCount++;
    }
}
