import { TurboTool } from "../../../../../build/turbodombuilder.esm";
export class DeleteTool extends TurboTool {
    constructor() {
        super(...arguments);
        this.toolName = "delete";
    }
    click(e, target) {
        if ("delete" in target && typeof target.delete === "function") {
            target.delete();
            return true;
        }
    }
}
