import { TurboSubstrate } from "../../../../build/turbodombuilder.esm";
export class PlaylistSubstrate extends TurboSubstrate {
    constructor() {
        super(...arguments);
        this.keyName = "substrate";
        this.substrateName = "playlist";
    }
    testAddingSong(song) {
        return !this.model.songs.find(entry => entry === song.id);
    }
}
